	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked.co.uk","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/javascript-testing-with-node-js-testacular-jasmine/index.md absPath of file >>> MarkdownRemark","html":"<p>For the last year or so I've been working on large scale (and complicated) Javascript applications, and one of the big deficiencies that exposed was the historically poor support for unit testable, metric-supported Javascript - at least within our own company. And so, for the last half year my 'hobby' has been improving our Javascript development, testing, build and deployment processes.</p>\n<p>Times move fast, and this presentation (below and <a href=\"https://docs.google.com/presentation/d/1S7btcTnZ7yrx4JgVVOdj4weYS3EPq6nbdPiFxdBvPnU/edit?usp=sharing\">here</a>) is already a little out of date, but it's the slides from an internal presentation/workshop I gave a few months ago which outlined how Testacular (now called Karma) can be used to execute unit tests and gather reliable code coverage metrics on Javascript projects.</p>\n<iframe src=\"https://docs.google.com/presentation/d/1S7btcTnZ7yrx4JgVVOdj4weYS3EPq6nbdPiFxdBvPnU/embed?start=false&amp;loop=false&amp;delayms=3000\" height=\"389\" width=\"480\" allowfullscreen=\"true\" frameborder=\"0\" style=\"max-width:100%;width:100%\"></iframe>\n<p>I'll be updating this presentation and giving a new version of it to the company soon, so I'll use that opportunity to throw a more expanded blog post up that outlines what this all means (and if you're lucky, with a script to explain it all!)</p>\n<p>Also, we're <a href=\"https://github.com/MMTDigital\">(MMT Digital)</a> is now on GitHub</a>, which is where you can find the (admittedly paltry) code offerings that accompany this presentation.</p>","frontmatter":{"title":"Javascript Testing with Node.js, Testacular & Jasmine","date":"May 06, 2013","tags":["javascript","karma","nodejs","presentations","testacular"]}}},"pathContext":{}}

